import id from '../index/myscript-compiled';

let likeNotesSpan = document.getElementById('likeNotesSpan');
let dislikeNotesSpan = document.getElementById('dislikeNotesSpan');

let buttonCanselAll = function () {
    $('.like').removeClass('active');
    let storeageLength = localStorage.length - 1;
    for (let i = 0; i < storeageLength; i++) {
        let data = {};
        if (localStorage.key(i) == 'lastId') {
            continue;
        }
        else {
            let key = localStorage.key(i);
            data = JSON.parse(localStorage.getItem(key));
            data.like = false;
            localStorage.setItem(key, JSON.stringify(data));
        }
    }
    id.likeSpan = 0;
    likeNotesSpan.innerHTML = 0;
    dislikeNotesSpan.innerHTML = storeageLength;
};

export default buttonCanselAll;